/*  <fonts>  */
@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/OpenSans-Regular.woff2") format('woff2'), url("../fonts/OpenSans-Regular.woff") format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/OpenSans-Bold.woff2") format('woff2'), url("../fonts/OpenSans-Bold.woff") format('woff');
  font-weight: bold;
  font-style: normal;
}
/*  </fonts>  */
/*  <vars>  */
/*  </vars>  */
/*  <general>  */
html {
  font-size: 62.5%;
  cursor: default;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-tap-highlight-color: #000;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  color: #777;
  font: normal 1.4rem/1.6 'Open Sans';
  background-color: #fff;
}
:-moz-placeholder {
  opacity: 1;
  color: currentColor;
}
:-ms-input-placeholder {
  opacity: 1;
  color: currentColor;
}
a {
  color: #5769a3;
  font: inherit;
  text-decoration: none;
  transition: all 0.2s;
}
a:hover {
  text-decoration: underline;
}
img {
  max-width: 100%;
}
.main {
  flex: 1;
}
/*  </general>  */
/*  <header>  */
.homepage-top {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.header {
  flex-shrink: 0;
  padding: 13px 0;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  width: 330px;
  margin-right: 100px;
}
.header__slogan {
  width: 150px;
  height: 10px;
  background: url("../i/slogan.svg") no-repeat;
}
@media screen and (max-width: 481px) {
  .header__slogan {
    display: none;
  }
}
.header__icons {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.header__icons-item {
  position: relative;
  width: 24px;
  height: 24px;
  margin-left: 60px;
}
.header__icons-item::before {
  position: absolute;
  top: 50%;
  left: -31px;
  width: 1px;
  height: 24px;
  background-color: #5769a3;
  content: '';
  transform: translateY(-50%);
  pointer-events: none;
}
.header__icons-item:first-child {
  margin-left: 0;
}
.header__icons-item:first-child::before {
  display: none;
}
.header__icons-item svg {
  width: 24px;
  height: 24px;
  fill: #5769a3;
}
/*  </header>  */
/*  <nav>  */
.nav {
  flex-shrink: 0;
  background-color: #5769a3;
}
@media screen and (max-width: 481px) {
  .nav {
    padding: 10px;
  }
  .nav__cover {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9998;
    background-color: rgba(0,0,0,0.6);
    content: '';
  }
}
.nav-menu {
  display: none;
}
@media screen and (max-width: 481px) {
  .nav-menu {
    display: flex;
    align-items: center;
    position: relative;
    width: 30px;
    height: 24px;
  }
  .nav-menu::before,
  .nav-menu::after {
    position: absolute;
    right: 0;
    left: 0;
    height: 2px;
    background-color: #fff;
    border-radius: 4px;
    content: '';
  }
  .nav-menu::before {
    top: 0;
  }
  .nav-menu::after {
    bottom: 0;
  }
  .nav-menu span {
    position: relative;
    width: 100%;
    height: 2px;
    background-color: #fff;
    border-radius: 4px;
  }
  .nav-menu span::after {
    position: absolute;
    top: 50%;
    left: calc(100% + 10px);
    color: #fff;
    font-size: 2.1rem;
    text-transform: uppercase;
    white-space: nowrap;
    content: attr(data-text);
    transform: translateY(-50%);
  }
}
.nav-main {
  display: flex;
}
@media screen and (max-width: 481px) {
  .nav-main {
    display: block;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 9999;
    overflow-y: auto;
    max-width: 90%;
    width: 340px;
    background-color: #5769a3;
    padding: 30px;
    transform: translateX(-100%);
    transition: all 0.5s;
  }
}
@media screen and (max-width: 481px) {
  .nav-main--active {
    box-shadow: 0 0 5px rgba(0,0,0,0.9);
    transform: translateX(0);
  }
}
.nav-main__close {
  display: none;
}
@media screen and (max-width: 481px) {
  .nav-main__close {
    display: block;
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 10000;
    width: 20px;
    height: 20px;
  }
  .nav-main__close::before,
  .nav-main__close::after {
    position: absolute;
    top: 50%;
    left: -25%;
    width: 28.799999999999997px;
    height: 2px;
    background-color: #fff;
    margin-top: -1px;
    content: '';
  }
  .nav-main__close::before {
    transform: rotate(45deg);
  }
  .nav-main__close::after {
    transform: rotate(-45deg);
  }
}
.nav-main__item {
  display: flex;
  align-items: center;
  position: relative;
  height: 75px;
  color: #fff;
  font-size: 2rem;
  text-transform: uppercase;
  margin-right: 46px;
}
@media screen and (max-width: 481px) {
  .nav-main__item {
    display: block;
    height: auto;
    margin-right: 0;
    margin-bottom: 15px;
  }
}
.nav-main__item::before {
  position: absolute;
  top: 73px;
  left: 0;
  max-width: 109px;
  width: 0;
  height: 2px;
  background-color: #fff;
  content: '';
  transition: all 0.2s;
}
.nav-main__item:hover {
  text-decoration: none;
}
.nav-main__item:hover::before {
  width: 100%;
}
@media screen and (max-width: 481px) {
  .nav-main__item:hover::before {
    display: none;
  }
}
@media screen and (min-width: 481px) {
  .nav-main__item--sub:hover .nav-main__sub {
    display: flex;
    flex-direction: column;
    animation: fadeIn 0.5s;
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 3;
    width: 286px;
    background-color: #fff;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16);
  }
}
.nav-main__item--active::before {
  position: absolute;
  top: 73px;
  left: 0;
  width: 100%;
  max-width: 109px;
  height: 2px;
  background-color: #fff;
  content: '';
}
@media screen and (max-width: 481px) {
  .nav-main__item--active::before {
    display: none;
  }
}
.nav-main__item-sub {
  position: relative;
  color: #fff;
  font-size: 2rem;
  padding-right: 30px;
}
a:hover.nav-main__item-sub {
  text-decoration: none;
}
.nav-main__item-sub::before {
  position: absolute;
  top: 50%;
  right: 0;
  border: solid transparent;
  border-width: 5px 3.5px 0;
  border-top-color: #fff;
  content: '';
  transform: translateY(-50%);
}
.nav-main__item::after {
  position: absolute;
  top: 50%;
  right: -23px;
  width: 1px;
  height: 24px;
  background-color: #fff;
  content: '';
  transform: translateY(-50%);
  pointer-events: none;
}
@media screen and (max-width: 481px) {
  .nav-main__item::after {
    display: none;
  }
}
.nav-main__item:last-child {
  margin-right: 0;
}
.nav-main__item:last-child::after {
  display: none;
}
.nav-main__sub {
  display: none;
}
@media screen and (max-width: 481px) {
  .nav-main__sub {
    display: block;
  }
}
.nav-main__sub-item {
  display: block;
  color: #707070;
  font-size: 2rem;
  text-transform: none;
  padding: 10px 32px 10px 49px;
}
@media screen and (max-width: 481px) {
  .nav-main__sub-item {
    color: #fff;
    padding: 10px 32px 10px 20px;
  }
}
a:hover.nav-main__sub-item,
.nav-main__sub-item--active {
  position: relative;
  color: #5769a3;
  text-decoration: none;
  background-color: #dbe0f4;
}
a:hover.nav-main__sub-item::after,
.nav-main__sub-item--active::after {
  animation: thrill 0.5s infinite;
  position: absolute;
  top: 50%;
  right: 27px;
  border: solid transparent;
  border-width: 3.5px 0 3.5px 5px;
  border-left-color: #5769a3;
  content: '';
  transform: translateY(-50%);
}
/*  </nav>  */
/*  <slider>  */
.slider {
  flex: 1;
}
.slider-item {
  width: 100%;
  height: 100%;
}
.slider-item__pic {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.slider-item__pic::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0,21,90,0.4);
  content: '';
}
.slider-item__pic img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.slider-item__descr {
  position: absolute;
  top: 50%;
  left: 50%;
  max-width: 90%;
  width: 495px;
  color: #fff;
  font-size: 2.4rem;
  text-align: center;
  transform: translate(-50%, -50%);
}
.slider-down {
  animation: sliderCirlce 2s infinite;
  position: absolute;
  bottom: 25px;
  left: 50%;
  z-index: 3;
  width: 32px;
  height: 32px;
  border: 1px solid #fff;
  border-radius: 50%;
  transform: translateX(-50%);
  pointer-events: none;
}
.slider-down::before {
  animation: sliderArrow 2s infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 6px;
  height: 6px;
  border: solid #fff;
  border-width: 0 1px 1px 0;
  content: '';
  transform: rotate(45deg) translateX(-75%);
}
.slider .flickity-page-dots {
  bottom: 75px;
  counter-reset: section;
}
.slider .flickity-page-dots .dot {
  position: relative;
  width: 48px;
  height: 22px;
  background: none;
  border-radius: 0;
  margin: 0 4px;
  opacity: 1;
}
.slider .flickity-page-dots .dot::before {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 2px;
  background-color: #fff;
  content: '';
}
.slider .flickity-page-dots .dot::after {
  position: absolute;
  bottom: 0;
  left: 50%;
  color: #fff;
  font-size: 1.2rem;
  line-height: 1;
  transform: translateX(-50%);
  counter-increment: section;
  content: counter(section, decimal-leading-zero);
}
.slider .flickity-page-dots .dot.is-selected::before {
  background-color: #465893;
}
.slider .flickity-page-dots .dot.is-selected::after {
  color: #465893;
}
/*  </slider>  */
