/*  <send-message>  */
.send-message {
  background-color: #e8e8e8;
  padding: 46px 0 52px;
}
.send-message__title {
  color: #777;
  font-size: 2.1rem;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 35px;
}
.send-message__form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 464px;
  margin: auto;
}
@media screen and (max-width: 481px) {
  .send-message__form {
    max-width: 100%;
  }
}
.send-message__form-input {
  position: relative;
  width: calc(50% - 22px);
}
.send-message__form-input input {
  width: 100%;
  height: 40px;
  color: #777;
  font-size: 1.4rem;
  background-color: transparent;
  border: 0;
  border-bottom: 1px solid #a7a7a7;
  padding: 0 3px;
}
.send-message__form-input input:focus {
  outline: none;
}
.send-message__form-input input + label {
  position: absolute;
  top: 50%;
  left: 3px;
  z-index: 3;
  color: #a7a7a7;
  font-size: 1.4rem;
  background-color: #e8e8e8;
  transform: translateY(-50%);
  transition: all 0.2s;
  pointer-events: none;
}
.send-message__form-input input:focus + label,
.send-message__form-input input:not(:placeholder-shown) + label {
  top: -2px;
}
.send-message__form-input input:invalid:not(:placeholder-shown) {
  border-bottom-color: #f00;
  box-shadow: none;
}
.send-message__form-textarea {
  width: 100%;
  height: 94px;
  color: #777;
  font-size: 1.4rem;
  background-color: #fff;
  border: 0;
  padding: 12px 15px;
  margin-top: 30px;
  resize: none;
}
.send-message__form-textarea:focus {
  outline: none;
}
.send-message__form-submit {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 55px;
}
