    padding: 30px 15px;
  }
}
.modal-send__close {
  position: absolute;
  top: 30px;
  right: 10px;
  width: 18px;
  height: 18px;
}
.modal-send__close::before,
.modal-send__close::after {
  position: absolute;
  top: 50%;
  left: -25%;
  width: 25.919999999999998px;
  height: 2px;
  background-color: #707070;
  margin-top: -1px;
  content: '';
  transition: all 0.2s;
}
.modal-send__close::before {
  transform: rotate(45deg);
}
.modal-send__close::after {
  transform: rotate(-45deg);
}
a:hover.modal-send__close::before,
a:hover.modal-send__close::after {
  background-color: #030303;
}
